import React, { useEffect } from 'react'
import { makeStyles } from '@material-ui/core/styles'
import Paper from '@material-ui/core/Paper'
import Stepper from '@material-ui/core/Stepper'
import Step from '@material-ui/core/Step'
import StepLabel from '@material-ui/core/StepLabel'
import Typography from '@material-ui/core/Typography'
import ConnectionForm from '../connection/ConnectionForm'
import { useSelector, useDispatch } from 'react-redux'
import { RootState, createInvitation, sendCredential } from '@identity/core'
import { User } from './Faber'
import IssueCredentialForm from './IssueCredentialForm'

const useStyles = makeStyles(theme => ({
  appBar: {
    position: 'relative'
  },
  layout: {
    width: 'auto',
    marginLeft: theme.spacing(2),
    marginRight: theme.spacing(2),
    [theme.breakpoints.up(600 + theme.spacing(2) * 2)]: {
      width: 600,
      marginLeft: 'auto',
      marginRight: 'auto'
    },
    [theme.breakpoints.up(900 + theme.spacing(2) * 2)]: {
      width: 900,
      marginLeft: 'auto',
      marginRight: 'auto'
    }
  },
  paper: {
    marginTop: theme.spacing(3),
    marginBottom: theme.spacing(3),
    padding: theme.spacing(2),
    [theme.breakpoints.up(600 + theme.spacing(3) * 2)]: {
      marginTop: theme.spacing(6),
      marginBottom: theme.spacing(6),
      padding: theme.spacing(3)
    }
  },
  stepper: {
    padding: theme.spacing(3, 0, 5)
  }
}))

const steps = ['Create A Connection', 'Receive Digital Diploma']

export default function IssueCredentialPage({ user }: { user: User }) {
  const classes = useStyles()
  const [activeStep, setActiveStep] = React.useState(0)

  const dispatch = useDispatch()

  const { invitation } = useSelector((state: RootState) => state.invitation)
  const { connections } = useSelector((state: RootState) => state.connecion)
  const { credentialExchanges } = useSelector(
    (state: RootState) => state.issueCredential
  )

  const credentialExchange = credentialExchanges[0]

  useEffect(() => {
    dispatch(createInvitation())
  }, [dispatch])

  useEffect(() => {
    if (
      activeStep === 0 &&
      connections.length === 1 &&
      ['response', 'active'].includes(connections[0].state!)
    ) {
      setActiveStep(1)
    }
  }, [connections, activeStep])

  useEffect(() => {
    if (activeStep === 1 && credentialExchange?.state === 'credential_acked')
      setActiveStep(2)
  }, [credentialExchange, activeStep])

  const credential = {
    first_name: user.firstName!,
    last_name: user.lastName!,
    degree: user.degree!
  }
  const niceCredential = Object.entries(credential).reduce(
    (credentials, [name, value]) => ({
      ...credentials,
      [name
        .split('_')
        .map(word => word.charAt(0).toUpperCase() + word.slice(1))
        .join(' ')]: value
    }),
    {}
  )

  const onCredentialSubmit = () => {
    dispatch(
      sendCredential({
        connectionId: connections[0].connection_id!,
        comment: `Digital diploma from Faber College for ${user.firstName} ${user.lastName}`,
        attributes: credential
      })
    )
  }

  const stepContent = [
    <ConnectionForm invitation={JSON.stringify(invitation)} />,
    <IssueCredentialForm
      credential={niceCredential}
      onSubmit={onCredentialSubmit}
    />,
    <>
      <Typography variant="h6">
        Success you should now have your digital diploma
      </Typography>
      <Typography variant="body1" color="textSecondary" gutterBottom>
        Check the credential tab in the Identity app
      </Typography>
    </>
  ]

  return (
    <main className={classes.layout}>
      <Paper className={classes.paper}>
        <Typography component="h1" variant="h4" align="center">
          Digital Diploma for {user.firstName}
        </Typography>
        <Stepper activeStep={activeStep} className={classes.stepper}>
          {steps.map(label => (
            <Step key={label}>
              <StepLabel>{label}</StepLabel>
            </Step>
          ))}
        </Stepper>
        {stepContent[activeStep]}
      </Paper>
    </main>
  )
}
