import React from 'react'
import Layout from './Layout'
import { Switch, Route } from 'react-router-dom'
import LandingPage from './LandingPage'
import PresentProofPage from './PresentProofPage'
import { Config, getEventHandler } from '@identity/core'

const Acme = () => {
  const config = Config.getInstance()
  const eventHandlerConfig = {
    acaUrl: 'https://aca.acme.demo.animo.id',
    acaWhrUrl: 'wss://whr.acme.demo.animo.id/ws',
    acaWhrApiKey: '62517f51-a238-427a-9912-6c632ef12fe4'
  }

  config.setProperty('acaUrl', eventHandlerConfig.acaUrl)
  config.setProperty('acaWhrUrl', eventHandlerConfig.acaWhrUrl)
  config.setProperty('acaWhrApiKey', eventHandlerConfig.acaWhrApiKey)
  config.setProperty('eventHandler', getEventHandler(eventHandlerConfig))

  return (
    <Layout>
      <Switch>
        <Route path="/acme/apply">
          <PresentProofPage />
        </Route>
        <Route path="/acme">
          <LandingPage />
        </Route>
      </Switch>
    </Layout>
  )
}

export default Acme
