import React, { useState } from 'react'
import Layout from './Layout'
import {
  Switch,
  useRouteMatch,
  Route,
  useHistory,
  Redirect
} from 'react-router-dom'
import Login from './Login'
import LandingPage from './LandingPage'
import IssueCredentialPage from './IssueCredentialPage'
import { Config, getEventHandler } from '@identity/core'

export interface User {
  isLoggedIn: boolean
  email?: string
  firstName?: string
  lastName?: string
  degree?: string
}

const Faber = () => {
  const config = Config.getInstance()
  const eventHandlerConfig = {
    acaUrl: 'https://aca.faber.demo.animo.id',
    acaWhrUrl: 'wss://whr.faber.demo.animo.id/ws',
    acaWhrApiKey: '1e08eef8-1808-414c-b685-edce8b034ee4'
  }

  config.setProperty('acaUrl', eventHandlerConfig.acaUrl)
  config.setProperty('acaWhrUrl', eventHandlerConfig.acaWhrUrl)
  config.setProperty('acaWhrApiKey', eventHandlerConfig.acaWhrApiKey)
  config.setProperty('eventHandler', getEventHandler(eventHandlerConfig))

  const history = useHistory()
  const [user, setUser] = useState<User>({
    isLoggedIn: false,
    email: undefined,
    firstName: undefined,
    lastName: undefined,
    degree: 'SSI Guru'
  })
  let match = useRouteMatch()

  const onLoginSubmit = (email: string) => {
    const [firstName, lastName] = email
      .split('@')[0]
      .split('.')
      .map((name) => name.charAt(0).toUpperCase() + name.slice(1))
    setUser({
      ...user,
      email,
      isLoggedIn: true,
      firstName,
      lastName
    })

    history.replace('/faber')
  }

  const onLogout = () => {
    setUser({
      ...user,
      isLoggedIn: false,
      email: undefined,
      firstName: undefined,
      lastName: undefined
    })

    history.replace('/faber')
  }

  return (
    <Layout isLoggedIn={user.isLoggedIn} onLogout={onLogout}>
      <Switch>
        {user.isLoggedIn ? (
          <Redirect from={`${match.path}/login`} to="/faber" />
        ) : (
          <Route path={`${match.path}/login`}>
            <Login onSubmit={onLoginSubmit} />
          </Route>
        )}
        {user.isLoggedIn ? (
          <IssueCredentialPage user={user} />
        ) : (
          <LandingPage />
        )}
      </Switch>
    </Layout>
  )
}

export default Faber
