import React, { useEffect } from 'react'
import { makeStyles } from '@material-ui/core/styles'
import Paper from '@material-ui/core/Paper'
import Stepper from '@material-ui/core/Stepper'
import Step from '@material-ui/core/Step'
import StepLabel from '@material-ui/core/StepLabel'
import Typography from '@material-ui/core/Typography'
import ConnectionForm from '../connection/ConnectionForm'
import { useSelector, useDispatch } from 'react-redux'
import { RootState, createInvitation, sendProofRequest } from '@identity/core'
import PresentProofForm from './PresentProofForm'
import { Grid, TextField, Box, Avatar } from '@material-ui/core'
import VerifiedUserIcon from '@material-ui/icons/VerifiedUser'

const useStyles = makeStyles(theme => ({
  appBar: {
    position: 'relative'
  },
  layout: {
    width: 'auto',
    marginLeft: theme.spacing(2),
    marginRight: theme.spacing(2),
    [theme.breakpoints.up(600 + theme.spacing(2) * 2)]: {
      width: 600,
      marginLeft: 'auto',
      marginRight: 'auto'
    },
    [theme.breakpoints.up(900 + theme.spacing(2) * 2)]: {
      width: 900,
      marginLeft: 'auto',
      marginRight: 'auto'
    }
  },
  paper: {
    marginTop: theme.spacing(3),
    marginBottom: theme.spacing(3),
    padding: theme.spacing(2),
    [theme.breakpoints.up(600 + theme.spacing(3) * 2)]: {
      marginTop: theme.spacing(6),
      marginBottom: theme.spacing(6),
      padding: theme.spacing(3)
    }
  },
  stepper: {
    padding: theme.spacing(3, 0, 5)
  },
  icon: {
    color: theme.palette.success.dark,
    fontSize: '8rem'
  }
}))

const steps = ['Create A Connection', 'Provide Digital Diploma']

export default function PresentProofPage() {
  const classes = useStyles()
  const [activeStep, setActiveStep] = React.useState(0)

  const dispatch = useDispatch()

  const { invitation } = useSelector((state: RootState) => state.invitation)
  const { connections } = useSelector((state: RootState) => state.connecion)
  const { presentationExchanges } = useSelector(
    (state: RootState) => state.presentProof
  )

  let niceCredential: { [key: string]: string } | null = null

  if (presentationExchanges.length > 0) {
    const presentationExchange = presentationExchanges[0]
    if (presentationExchange.state === 'presentation_received') {
      if (activeStep === 1) setActiveStep(2)
      niceCredential = Object.entries(
        presentationExchange.presentation?.requested_proof.revealed_attrs!
      ).reduce(
        (creds, [name, { raw }]) => ({
          ...creds,
          [name
            .split('_')
            .map(word => word.charAt(0).toUpperCase() + word.slice(1))
            .join(' ')]: raw
        }),
        {}
      )
    }
  }

  useEffect(() => {
    dispatch(createInvitation())
  }, [dispatch])

  useEffect(() => {
    const sendProof = () => {
      dispatch(
        sendProofRequest({
          connectionId: connections[0].connection_id!
        })
      )
    }

    if (
      activeStep === 0 &&
      connections.length === 1 &&
      ['response', 'active'].includes(connections[0].state!)
    ) {
      setActiveStep(1)
      sendProof()
    }
  }, [connections, activeStep, dispatch])

  const stepContent = [
    <ConnectionForm invitation={JSON.stringify(invitation)} />,
    <PresentProofForm />,
    <>
      <Typography variant="h6">
        Thank you, we successfully received your diploma
      </Typography>
      <Typography variant="body1" color="textSecondary" gutterBottom>
        We will get back to you as soon as possible
      </Typography>
      <Grid container direction="row">
        <Grid sm={9}>
          {niceCredential &&
            Object.entries(niceCredential).map(([label, value]) => (
              <Grid item sm={9} md={4}>
                <TextField
                  fullWidth
                  color="secondary"
                  label={label}
                  variant="outlined"
                  disabled
                  value={value}
                  margin="normal"
                />
              </Grid>
            ))}
        </Grid>
        <Grid>
          <VerifiedUserIcon className={classes.icon} />
        </Grid>
      </Grid>
    </>
  ]

  return (
    <main className={classes.layout}>
      <Paper className={classes.paper}>
        <Typography component="h1" variant="h4" align="center">
          Digital Enrollment ACME
        </Typography>
        <Stepper activeStep={activeStep} className={classes.stepper}>
          {steps.map(label => (
            <Step key={label}>
              <StepLabel>{label}</StepLabel>
            </Step>
          ))}
        </Stepper>
        {stepContent[activeStep]}
      </Paper>
    </main>
  )
}
