import React from 'react'
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'
import QRCode from 'qrcode.react'

export default function ConnectionForm({ invitation }: { invitation: any }) {
  return (
    <React.Fragment>
      <Typography variant="h6">
        Scan the QR Code with your Identity app
      </Typography>
      <Typography variant="body1" color="textSecondary" gutterBottom>
        This allows you to establish a secure connection
      </Typography>
      <Grid container spacing={5}>
        <Grid item>
          {invitation && (
            <QRCode
              includeMargin
              style={{ borderRadius: 10 }}
              value={invitation}
              size={250}
            />
          )}
        </Grid>
      </Grid>
    </React.Fragment>
  )
}
