import React from 'react'
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom'
import Faber from './features/faber/Faber'
import Acme from './features/acme/Acme'

const Routing = () => {
  return (
    <Router>
      <Switch>
        <Route path="/faber">
          <Faber />
        </Route>
        <Route path="/acme">
          <Acme />
        </Route>
      </Switch>
    </Router>
  )
}

export default Routing
